@font-face {
    font-family: Texta;
    src: url('EncodeSans-Regular.ttf');
}

body {
    font-family: Texta;
    background-color: #e5e5e5;
}

.ui-widget input, .ui-widget select, .ui-widget textarea, .ui-widget button {
    font-family: Texta !important;
}

/* .ui-widget {
    font-size: 0.84em !important;
} */

.ui-cell-data {
    font-size: 0.84em !important;    
}

.ui-column-title {
    font-size: 0.84em !important;    
}


label {
    margin-bottom: 0 !important;
}

.coluna-acoes {
    text-align: center !important;
    padding: .25em .5em !important;
}

.coluna-nome {
    text-align: left !important;
}

.ui-button {
    margin-right: 0 !important;
    margin-left: .25em;
}

.panelFundoModal {
    position: fixed;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    padding: 2.5em 0;
    background: rgba(0,0,0,0.5);
    color: #ffffff;
    z-index: 999;
    -webkit-align-items: center;
    align-items: center;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
    justify-content: center;
}

.imgStatusProcessando {
    height: 32px;
    width: 32px;
}

.row {
    margin-top: 4px;
}

.ui-button {
    background-color: #3DA850 !important;
    border-color: #3DA850 !important;
    color: white !important;
}

.ui-button:hover {
	color: #3DA850 !important;
	background-color: white !important;
}

.ui-accordion-tab {
    border: 1px solid #d6d6d6;
}

.ui-accordion .ui-accordion-header {
    border: none !important;
}

.p-datatable table {
    table-layout: auto !important;
}

.p-column-title {
    white-space: nowrap;
}

.p-button {
    margin-left: 4px !important;
    margin-right: 4px !important;
}

.p-scrollpanel-content {
    overflow: scroll !important;
}

@media (min-width: 35em) {
    .coluna-codigo {
        text-align: center;
        width: 6em;
    }

    .coluna-acoes {
        width: 7em;
    }

    .coluna-valor {
        text-align: right !important;
    }

    .coluna-data {
        text-align: right !important;
    }

    .coluna-tipo {
        text-align: center !important;
    }
}

.coluna-tipo {
    white-space: nowrap;
}

.p-inputtext {
    padding: 4px !important;
}

.p-dropdown-label {
    padding-right: 16px !important;
}

body .p-datatable .p-datatable-thead > tr > th {
    padding: 6px;
    font-size: 13px;
    white-space: normal;
}

body .p-datatable .p-datatable-tbody > tr > td {
    padding: 6px;
}

body .coluna-acoes .p-button.p-button-icon-only {
    width: 24px;
    height: 24px;
}

body .p-checkbox {
    width: 20px;
    height: 20px;
    vertical-align: top;
}

body .p-checkbox .p-checkbox-box .p-checkbox-icon {
    font-size: 16px;
}

.layout-wrapper .layout-sidebar ul li a span {
    font-weight: 700;
}

button, input, optgroup, select, textarea {
    padding-left: 4px;
    padding-right: 4px;
}

.text-field-time > div:before {
    border: none !important;
}

.text-field-time > div > input {
    font-family: "Open Sans", "Helvetica Neue", sans-serif;
    font-size: 14px;
    padding: 0;
}

.text-field-time > div > input::-ms-clear {
    display: none;
}

.text-field-time > div > input::-webkit-clear-button {
    display: none;
}

.text-field-time > div > input::-ms-clear {
    width: 0;
    height: 0;
}

.text-field-time > div > input::-webkit-inner-spin-button,
.text-field-time > div > input::-webkit-outer-spin-button {
    -webkit-appearance: none; 
    margin: 0; 
}
